@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@300;400;500;700&family=Roboto:wght@300;400;500;700&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* CSS reset end */
/* font */
/* normal */
* {
  box-sizing: border-box; }

img {
  display: block; }

.red {
  color: #A00404; }

.wrap {
  width: 1200px;
  margin: 0 auto; }

.name {
  font-family: 'Roboto', sans-serif;
  font-size: 80px;
  font-weight: 400;
  line-height: 106px;
  border-bottom: 2px solid #000;
  margin-top: 37px;
  margin-bottom: 48px; }

.pic {
  margin-bottom: 48px; }

.tittle {
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 32px;
  font-weight: 400;
  line-height: 43px;
  padding-bottom: 8px;
  border-bottom: 1px solid #000;
  margin-bottom: 16px;
  /* span內英文垂直置中 */
  display: flex;
  align-items: center; }

.en {
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  margin-left: 16px; }

.info {
  margin-bottom: 72px; }

.info li {
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
  margin-bottom: 24px; }

.skill-area {
  background-color: #415165;
  padding-top: 24px;
  padding-bottom: 36px;
  margin-bottom: 72px; }

.title-skill {
  width: 996px;
  margin: 0 auto;
  color: #FFF;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 32px;
  font-weight: 400;
  line-height: 45px;
  text-align: center;
  padding-bottom: 8px;
  border-bottom: 1px solid #FFF;
  margin-bottom: 37px;
  /* span內英文垂直置中 */
  display: flex;
  align-items: center;
  justify-content: center; }

.skill {
  width: 217px;
  margin: 0 auto;
  padding-left: 24px;
  border-left: 1px solid #96A9C4; }

.skill li {
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: 300;
  line-height: 36px;
  color: #FFF;
  margin-bottom: 16px; }

.exp {
  margin-left: 24px;
  border-left: 1px solid #96A9C4;
  padding-left: 24px; }

.exp img {
  padding: 8px 0; }

.exp p {
  font-size: 20px;
  line-height: 30px; }

footer {
  background-color: #415165;
  text-align: center;
  color: #FFF;
  padding: 24px 0 86px 0; }

.title-contact {
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 32px;
  font-weight: 400;
  line-height: 45px;
  padding-bottom: 8px;
  border-bottom: 1px solid #FFF;
  margin-bottom: 32px;
  /* span內英文垂直置中 */
  display: flex;
  align-items: center;
  justify-content: center; }

.contact li {
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: 300;
  line-height: 36px;
  margin-bottom: 16px; }

.contact li a {
  text-decoration: none;
  color: #FFF; }

.contact li a:hover {
  color: #96A9C4; }

.icon {
  display: flex;
  justify-content: center;
  padding-top: 20px; }

.icon li {
  padding: 0 12px; }

.icon li a img:hover {
  opacity: 0.8; }
